const amap = require('../../libs/amap-wx.js')
export default {
	data() {
		return {
			amapPlugin: null,
			key: "6799b5f6f88d3d9fb52ac244855a8759",
			obj: {}
		}
	},
	onLoad() {
		this.amapPlugin = new amap.AMapWX({
			key: this.key
		})
		this.getWeather()
	},
	methods: {
		//获取天气数据
		getWeather: function() {
			uni.showLoading({
				title: '请稍候...'
			})
			this.amapPlugin.getWeather({
				success: (data) => {
					//成功回调
					//console.log(data)
					uni.hideLoading()
					this.obj= data
				},
				fail: function(info) {
					//失败回调
					console.log(info)
				}
			})
		},
		back() {
			uni.navigateBack()
		}
	}
}
